import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RatingService {

  private baseUrl = 'http://votacao.fbfagostousa.com:8081/';

  constructor(private http: HttpClient) { }

  public checkUserInfo$(email: string): Observable<any> {

    const checkInfoUrl = 'usuario/login/';
    return this.http.post(this.baseUrl + checkInfoUrl + email, null);

  }
  public updateUserData(email, userInfo): Observable <any> {

    const updateInfoUrl = 'usuario';
    console.log(email)

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'email': email
      })
    };

    return this.http.put(this.baseUrl + updateInfoUrl, userInfo, httpOptions);

  }

  public getPlateData(id): Observable <any> {

    const getPlateUrl = 'plato/';
    return this.http.get(this.baseUrl + getPlateUrl + id);
  }

  public vote(email, id, garzom, votes): Observable <any> {

    const voteUrl = 'plato/' + id + '/votar/garzon/';

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'email': email
      })
    };

    const fullUrl = this.baseUrl + voteUrl + garzom;

    return this.http.post(fullUrl, votes, httpOptions);
  }

  public getCountries(): Observable<any> {
    return this.http.get(this.baseUrl + 'pais/findAll');
  }
}
