import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';

import { AuthService, SocialUser } from 'angularx-social-login';
import { FacebookLoginProvider, GoogleLoginProvider, LoginOpt } from 'angularx-social-login';
import {RatingService} from '../rating.service';
import {MatStepper} from '@angular/material';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'fbf-rating-form',
  templateUrl: './rating-form.component.html',
  styleUrls: ['./rating-form.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RatingFormComponent implements OnInit {

  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  garzonForm: FormGroup;

  user: SocialUser;
  loggedIn: boolean;
  garzom: string;
  userEmail: string;
  showRatingErrorMessage: boolean = false;
  idPlato: number;
  isLinear: boolean = true;
  countries = [];

  plateData = {};

    @ViewChild('stepper') stepper: MatStepper;

  categories = [];

  googleLoginOptions: LoginOpt = {
    scope: 'profile email'
  };

  fbLoginOptions: LoginOpt = {
    scope: 'pages_messaging,pages_messaging_subscriptions,email,pages_show_list,manage_pages',
    return_scopes: true,
    enable_profile_selector: true
  };

  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService,
    private ratingService: RatingService,
    private route: ActivatedRoute,
    private router: Router) {

    console.log("params: ",this.route.snapshot.params);

    this.idPlato = this.route.snapshot.params['id_plato'];
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID, this.googleLoginOptions).then(function(respuesta) {
      //console.log(respuesta);
    });
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID, this.fbLoginOptions);
  }

  signInWithEmail() {
    this.userEmail = this.firstFormGroup.value.userEmail;

    this.checkUserInfo(this.userEmail);

  }

  signOut(): void {
    this.stepper.selected.optional = false;
    this.authService.signOut();
  }

  move(index: number) {
    this.stepper.selectedIndex = index;
  }

  updateInfo() {
    console.log(this.secondFormGroup.value);
    const userinfo = this.secondFormGroup.value;
    this.ratingService.updateUserData(this.userEmail, userinfo).subscribe(result => {
      console.log(result);
      this.stepper.next();
    }, error => {
      console.log(error);
    });
  }

  getRatingValues() {
    console.log(this.categories);
    let votes = [];

    for (const cat of this.categories) {

      const obj = {
        'puntuacion': cat.selectedValue,
        'caracteristica': {'id': cat.id, 'clave': cat.clave}
      }
      votes.push(obj);
    }

    this.ratingService.vote(this.userEmail, this.idPlato, this.garzom, votes).subscribe(result => {
      console.log(result);
      this.showRatingErrorMessage = false;
      this.stepper.next();
    }, error => {
      console.log("error: ", error)
      if(error.status == 302)
        this.showRatingErrorMessage = true;
      this.stepper.next();

    });
  }

  checkUserInfo(email) {
    this.ratingService.checkUserInfo$(email).subscribe(result => {
      console.log('result: ', result);
      this.secondFormGroup.setValue(result);
      // this.secondFormGroup.value.pais=2;
      if (result.nombre != null && result.telefono != null && result.pais != null && result.estado != null && result.ciudad != null ) {
        this.move(2);
      } else {
        this.move(1);
      }
    }, error => {
      this.move(1);
    });
  }

  compareFn(f1:any, f2:any):boolean {
    return f1.id == f2.id;
  }

  ngOnInit() {

    this.firstFormGroup = this.formBuilder.group({
      userEmail: ['', [Validators.required, Validators.email]]
    });
    this.secondFormGroup = this.formBuilder.group({
      id: '',
      email: '',
      facebookId: '',
      googleId: '',
      nombre: ['', Validators.required],
      telefono: ['', Validators.required],
      pais: ['', Validators.required],
      estado: ['', Validators.required],
      ciudad: ['', Validators.required]
    });

    this.ratingService.getPlateData(this.idPlato).subscribe(result => {
      this.plateData = result;
      console.log(this.plateData);
      for (const cat of result.categoria.caracteristicas){
        cat.selectedValue = '6';
        this.categories.push(cat);
      }

      this.authService.authState.subscribe((user) => {

        console.log('user', user);

        this.user = user;
        this.loggedIn = (user != null);

        if (user != null) {
          this.userEmail = user.email;
          this.stepper.selected.optional = true;
          this.checkUserInfo(this.userEmail);
        }
      });

    }, error => {
        this.router.navigate(['/notFound']);
    });

    this.ratingService.getCountries().subscribe(result => {
      console.log("countries: ", result);
      this.countries =  result;
    });



  }

}
